<template>
  <span>user:{{user}}</span>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
  data() {
    return {
      user: "111111111"
    }
  },
  computed: {
    // 使用对象展开运算符将 getter 混入 computed 对象中
    ...mapGetters([
      'getUserInfo',
    ])
  }, mounted() {
    this.$store.commit("setUserInfo", 1129001);
    console.log(this.getUserInfo)
    this.user = this.getUserInfo;

  }
}
</script>



